import { PageScaffold } from "@/components/page-scaffold";

export default function TemplatesPage() {
  return (
    <PageScaffold
      code="PRO-13"
      title="Modelos de protocolo"
      objective="Listagem de modelos com uso, tags e ações de editar/aplicar."
      primaryAction={{ label: "Aplicar modelo", href: "/templates/apply" }}
      sections={[
        "Tabela de modelos",
        "Uso e atualização",
        "Ações de editar/aplicar",
      ]}
    />
  );
}
